import { Table, Form, Extend } from "./tableExtend";

// 
const components = [Table, Form, Extend];

//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;
  // 
  components.map((component) => {
    Vue.component(component.name, component);
  });
};

// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  //  install Vue.use() 
  install,
  // 
  Table,
  Form,
  Extend,
};
